const app = getApp().globalData;
const {
	imgRemote
} = app;
export default {
	props: ['ware', 'config'],
	data() {
		return {
			imgRemote: imgRemote,
			token: uni.getStorageSync('cdj_token')
		}
	},
	methods: {
		showCart() {
			this.$emit('showCart')
		},
		detail(){
			if(this.config.is_detail==1){
				uni.navigateTo({
				url:`/pages/index/shopdetail?id=${this.ware.id}`
			})
			}
		}
	}
}
